.Inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.Inputs-fields {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Inputs-field.Inputs-field {
  min-width: 16rem;
  margin: 1rem 0.5rem;
}

@media screen and (max-width: 768px) {
  .Inputs-field.Inputs-field {
    min-width: 100%;
    margin: 0.5rem 0;
  }
}

.Inputs-buttons {
  margin-bottom: 1rem;
}

.Inputs-icon {
  margin-right: 0.6rem;
}



